img {
    max-width: 100%;
}

button {
    user-select: none;
}

.body {
    background: $color-bg;
    color: $color-text;

    font-family: $font-family;
    font-size: $font-size;
    letter-spacing: $letter-spacing;
    line-height: $line-height;

    @include media-breakpoint-up(md) {
        font-size: $font-size-lg;
        line-height: $line-height-lg;
    }

    &._frozen {
        overflow: hidden;
        height: 100%;
    }

}

.container {
    @include make-container();
}

.hidden {
    display: none !important;
}

.hidden-sm {
    @include media-breakpoint-down(sm) {
        display: none !important;
    }
}

.hidden-lg {
    @include media-breakpoint-up(lg) {
        display: none !important;
    }
}

.visible-sm {
    @include media-breakpoint-up(md) {
        display: none !important;
    }
}

.visible-md {
    @include media-breakpoint-down(sm) {
        display: none !important;
    }

    @include media-breakpoint-up(lg) {
        display: none !important;
    }
}

.visible-lg {
    @include media-breakpoint-down(md) {
        display: none !important;
    }
}

.nowrap {
    white-space: nowrap;
}

.muted {
    opacity: .5;
}

.dashed {
    border-bottom: 1px dashed;
}

a,
.link {
    cursor: pointer;
    color: $color-link;
}

strong {
    font-weight: bold;
}

::placeholder {
    color: rgba($color-text, .5);
}

:focus::placeholder {
    opacity: 1;
}

.necessaty_fields {
    display: none !important;
}

.ios-fixed {
    @include media-breakpoint-down(sm) {
        position: fixed;
        overflow: hidden;
        height: 100%;
        width: 100%;
    }
}

.lazyload {
    visibility: hidden;
}

.fancybox-navigation {
    position: static;
}

.dev-menu {
    @include media-breakpoint-up(md) {
        column-count: 2;
    }

    @include media-breakpoint-up(lg) {
        column-count: 4;
    }

    &__item {
        display: block;
        margin-bottom: 1em;
        page-break-inside: avoid;
    }
}

.title {
    font-family: 'Cormorant Garamond', serif;
    font-size: 36px;
    line-height: 117.3%;
    /* or 42px */
    letter-spacing: -0.025em;
    font-weight: 300;

}

.mainForm {
    // justify-content: space-between;
    // display: flex;
    // flex-direction: column;

    &__input {
        border-bottom: 1px solid #9D9D9D;
        padding-bottom: 27px;
        padding-left: 16px;
        // width: 270px;
        width: 100%;
        padding-bottom: 21px;
        transition: border-color 0.2s ease-in-out;
        // margin-bottom: 5px;
        // &:focus {
        //     border-color: #000000;
        // 



        &-group {
            // width: 100%;
            // display: flex;
            // justify-content: space-between;
            // flex-wrap: wrap;
            display: flex;
            flex-direction: column;
            position: relative;
            margin-bottom: 5px;

            &:focus-within {
                // .mainForm__label {
                //     bottom: 100%;
                //     font-size: 12px;
                //     color: #181818;
                // }

                .mainForm__input {
                    border-color: #181818;

                }
            }



        }
    }

    &__label {
        font-size: 16px;
        // margin-bottom: 5px;
        // color: sandybrown;
        position: absolute;
        bottom: 21px;
        left: 16px;
        color: #585858;
        transition: bottom 0.2s ease-in-out, font-size 0.2s ease-in-out, color 0.2s ease-in-out;

        &--active {
            bottom: 100%;
            font-size: 12px;
            color: #181818;
        }
    }
}


.button {
    width: 100%;
    padding: 17px 0;
    text-align: center;
    color: $color-white;
    line-height: 120.3%;
    /* identical to box height, or 17px */
    letter-spacing: 0.07em;
    text-transform: uppercase;
    font-family: 'Averta', sans-serif;
    z-index: 50;
    cursor: pointer;

    &--primary {
        background-color: $color-red;
    }

    &--light {
        color: #333333;
        background-color: #F1F1F1;
    }

    &--dark {
        background-color: #292929;

    }

    &--bordered {
        border: 1px solid #323232;
        background-color: transparent;
        color: #131313;
    }
}

.policy {
    font-size: 12px;
    line-height: 118.49%;
    /* or 14px */
    text-align: center;
    letter-spacing: 0.035em;
    color: #585858;

    a {
        color: $color-red;
    }
}

.bg-image {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
}

.swiper {
    &-button {
        &-next {
            width: 54px !important;
            height: 54px !important;
            border-radius: 50%;
            background-color: $color-red;

            &::after {
                // content: url('../img/swiper-button-next.svg') !important;
                content: '' !important;
                // width: 54px !important;
                // height: 54px !important;
                width: 15px !important;
                height: 15px !important;
                border-top: 3px solid #FFFFFF;
                border-right: 3px solid #FFFFFF;
                transform: rotate(45deg);
                margin-right: 5px;
                // background: url('../img/swiper-button-next.svg') no-repeat center;

            }
        }

        &-prev {
            left: 0 !important;
            width: 54px !important;
            height: 54px !important;
            border-radius: 50%;
            background-color: $color-red;

            &::after {
                // content: url('../img/swiper-button-next.svg') !important;
                content: '' !important;
                // width: 54px !important;
                // height: 54px !important;
                width: 15px !important;
                height: 15px !important;
                border-top: 3px solid #FFFFFF;
                border-right: 3px solid #FFFFFF;
                transform: rotate(-135deg);
                margin-left: 5px;
                // background: url('../img/swiper-button-next.svg') no-repeat center;

            }
        }
    }

    &-disabled {
        transform: translate3d(0px, 0, 0) !important;
    }

    &-scrollbar-drag {
        background-color: $color-red;
    }

    &-scrollbar {
        height: 2px !important;
    }

}

.status {
    font-size: 12px;
    line-height: 15px;

    /* identical to box height */
    &-item {
        color: #131313;

        &--active {
            color: #868686;
        }
    }

}

@media(max-width:1280px) {

    .overflow-hidden {
        overflow: hidden;
    }

    .overflow-auto {
        height: 100vh;
        overflow: auto;
    }
}
